/* tab.scss */
.tab_bar {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 6.4%;
  min-height: 54px;
  border-top: 1px solid #c1c1c1;
  background-color:rgba(255, 255, 255, 0.4);

  
  > ul {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    
    > li {
      flex: 1;
      text-align: center;
      transition: transform 500ms linear 0s;
      > a {
        color: #c0c1c2;
        
        > i {
          display: block;
          font-size: 22px;
        }
      }
    }
  }
}

.tab_bar > ul > li:hover {
  transform: scale(1.1);
}
.tab_bar > ul > li {
  &.active a {
    color: #513736;
  }
}